import "./GridBusiness.css";
import { motion } from "framer-motion";

const GridBusiness = () => {
  return (
    <div className="flexBox mt-10">
      <motion.div
        className="flexBox"
        initial={{ opacity: 0, x: -50 }}
        whileInView={{ opacity: 1, x: 0 }}
        viewport={{ once: true }}
        transition={{
          ease: "easeInOut",
          duration: 1,
        }}
      >
        <img
          className="gridPicture"
          src="/assets/business_header.png"
          alt="businessLogo"
        />
      </motion.div>
      <motion.div
        className="flexBox"
        initial={{ opacity: 0, x: -50 }}
        whileInView={{ opacity: 1, x: 0 }}
        viewport={{ once: true }}
        transition={{
          ease: "easeInOut",
          duration: 2,
        }}
      >
        <img className="gridPicture" src="/assets/grid.png" alt="gridLogo" />
      </motion.div>
      <motion.div
        className="flexBoxSide wd-set"
        initial={{ opacity: 0, x: -50 }}
        whileInView={{ opacity: 1, x: 0 }}
        viewport={{ once: true }}
        transition={{
          ease: "easeInOut",
          duration: 3,
        }}
      >
        <span className="bold-6">   </span>
        <span className="bold-5">
           /   /  /  / (,
           ) /  ( ) /   /  / 
          ( , P.0.P,  ) / () /  /
           / / (/ ) / POS /  or  /
          CCTV /  /  /   /  /  
          
        </span>
        <span className="bold-5">     .</span>
        <span className="bold-5">  - 20 (  )</span>
        <span className="bold-5">
                   .
        </span>
        <span className="bold-5">
                  .
        </span>
        <span className="bold-5">
           66m (20)     1  66m(20)
           .
        </span>
        <span className="bold-5">
                  .  
             .
        </span>
      </motion.div>
    </div>
  );
};

export default GridBusiness;
